import React, { Component } from 'react';
import './app.css';
import Button from '../button/button';

export default class App extends Component {
	constructor(props) {
		super(props);
		this.state = {
			formula: '',
			output: '0',
			currentValue: '',
		}
	}
	addNumbers = (value) => {
		if (this.state.output === '0') {
			this.setState({
				formula: this.state.formula + value,
				output: value,
				currentValue: this.state.currentValue + value
			})
		}
		else if(this.state.output.length>15){
			this.setState({
				output:'too much numbers',
			})
			setTimeout(()=>{
				this.setState({
					output:this.state.currentValue
				})
			},500)
		}
		else {
			this.setState({
				formula: this.state.formula + value,
				output: this.state.output + value,
				currentValue: this.state.currentValue + value
			})
		}
	}
	addAction = (value) => {
		let lastChar = this.state.formula.slice(-1);
		if ((/[+-/*]/).test(lastChar)) {
			let newFormula = this.state.formula.replace(/.$/, value)
			this.setState({
				formula: newFormula,
				output: '0',
				currentValue: ''
			})
		}
		else {
			this.setState({
				formula: this.state.formula + value,
				output: '0',
				currentValue: ''
			})
		}
	}
	reset = () => {
		this.setState({
			formula: '',
			output: '0',
			currentValue: ''
		})
	}
	delete = () => {
		let newFormula = this.state.formula.slice(0, -1);
		let newOutput = this.state.output === '0' ? '0' : this.state.output.slice(0, -1);
		this.setState({
			formula: newFormula,
			output: newOutput
		})
	}
	equals = () => {
		try{
			let equalsNumber = eval(this.state.formula);
			this.setState({
				formula: String(equalsNumber),
				output: '0',
				currentValue: '',
			})
		}
		catch{
			this.setState({
				formula: 'wrong number',
				output: '0',
				currentValue: '',
			})
		}
	}
	addDecimal = (value) => {
		if (this.state.currentValue.includes(value)) {
			return false
		}
		else {
			this.setState({
				formula: this.state.formula + value,
				output: this.state.output + value,
				currentValue: this.state.currentValue + value
			})
		}
	}
	render() {
		return (
			<div className="app-wrap">
				<div className="calculator">
					<div className="formula-screen">{this.state.formula}</div>
					<div className="output-screen">{this.state.output}</div>
					<div className="panel">
						<Button id="one" value="1" onToggleClick={this.addNumbers}>1</Button>
						<Button id="two" value="2" onToggleClick={this.addNumbers}>2</Button>
						<Button id="three" value="3" onToggleClick={this.addNumbers}>3</Button>
						<Button id="four" value="4" onToggleClick={this.addNumbers}>4</Button>
						<Button id="five" value="5" onToggleClick={this.addNumbers}>5</Button>
						<Button id="six" value="6" onToggleClick={this.addNumbers}>6</Button>
						<Button id="seven" value="7" onToggleClick={this.addNumbers}>7</Button>
						<Button id="eight" value="8" onToggleClick={this.addNumbers}>8</Button>
						<Button id="nine" value="9" onToggleClick={this.addNumbers}>9</Button>
						<Button id="zero" value="0" onToggleClick={this.addNumbers}>0</Button>
						<Button id="reset" value="AC" onToggleClick={this.reset}
							style={{
								background: '#a71d1d'
							}}>AC
						</Button>
						<Button id="delete" value="C" onToggleClick={this.delete}
							style={{
								background: '#a71d1d'
							}}>C
						</Button>
						<Button id="divide" value="/" onToggleClick={this.addAction}
							style={{
								background: '#9fa2a6'
							}}>/
						</Button>
						<Button id="multiply" value="*" onToggleClick={this.addAction}
							style={{
								background: '#9fa2a6'
							}}>x
						</Button>
						<Button id="minus" value="-" onToggleClick={this.addAction}
							style={{
								background: '#9fa2a6'
							}}>-
						</Button>
						<Button id="plus" value="+" onToggleClick={this.addAction}
							style={{
								background: '#9fa2a6'
							}}>+
						</Button>
						<Button id="decimal" value="." onToggleClick={this.addDecimal}>.</Button>
						<Button id="equals" value="=" onToggleClick={this.equals}
							style={{
								background: '#140d56'
							}}>=
						</Button>
					</div>
				</div>
			</div>
		)
	}
}